// react imports
import React, { Component } from 'react';
import { Link } from 'react-router-dom';

//images
import logo from '../../static/images/logo.png';

// css
import '../../static/styles/components/BasePage/NavBarWeb.css';

//urls
import { REGISTER_DET, link, msg, } from '../../static/utils/applicationUrls';

export default class NavBarWeb extends Component {
	constructor(props) {
		super(props);
		this.state = {
			prevScrollpos: window.pageYOffset,
			visible: true,
		};
	}

	// Adds an event listener when the component is mount.
	componentDidMount() {
		window.addEventListener('scroll', this.handleScroll);
	}

	// Remove the event listener when the component is unmount.
	componentWillUnmount() {
		window.removeEventListener('scroll', this.handleScroll);
	}

	// Hide or show the menu.
	handleScroll = () => {
		const { prevScrollpos } = this.state;
		const currentScrollPos = window.pageYOffset;
		const visible = prevScrollpos > currentScrollPos;
		this.setState({
			prevScrollpos: currentScrollPos,
			visible,
		});
	};

	render() {
		return (
			<div className='WebNavBarHideInMobile'>
				<div className={this.state.visible ? 'WebNavbar' : 'navhide'}>
					<div
						className='WebNavbar__logo'
						onClick={(event) => {
							event.preventDefault();
							window.location.href = '/';
						}}>
						<div className='WebNavbar__logo--set'>
							<img className='logo' src={logo} alt='logo'></img>
						</div>
					</div>
					<div className='WebNavbar__menu'>
						<Link to='/det'>
							<div className='WebNavbar__menu--items'>
								About Us
							</div>
						</Link>
						<Link to='/benefits'>
							<div className='WebNavbar__menu--items'>
								Trip Benefits
							</div>
						</Link>
						<Link to='/empire'>
							<div className='WebNavbar__menu--items'>
								The Empire
							</div>
						</Link>
						<a
							href={REGISTER_DET}
							className='WebNavbar__menu--items'
							target='_blank'>
							<button className='btn--ticket'>Sign in</button>
						</a>
						<a
							href={link + msg}
							className='WebNavbar__menu--items'
							target='_blank'>
							<button className='btn--ticket--golden'>
								Explore for Free
							</button>
						</a>
					</div>
				</div>
			</div>
		);
	}
}
