// react
import React from 'react';
// react carousel
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

// images
import Plane from '../../static/images/Icons/plane_in_button.png';
import RamBansal from '../../static/images/Mentors_&_Team/RamBansal.png';
import MayankBishnoi from '../../static/images/Mentors_&_Team/MayankBishnoi.png';
import Utkarsh from '../../static/images/Dreamers/Utkarsh.png';
import Ahmad from '../../static/images/Dreamers/Ahmad.png';

// config
import isMobile from '../../static/utils/applicationUrls';

export default function MentorsOverviewMobile() {
	const responsive = {
		desktop: {
			breakpoint: { max: 3000, min: 1024 },
			items: 1,
			slidesToSlide: 1, // optional, default to 1.
		},
		tablet: {
			breakpoint: { max: 1024, min: 464 },
			items: 1,
			slidesToSlide: 1, // optional, default to 1.
		},
		mobile: {
			breakpoint: { max: 464, min: 0 },
			items: 1,
			slidesToSlide: 1, // optional, default to 1.
		},
	};

	const mentorCardsData = [
		{
			name: 'Ram Bansal',
			designation: 'Founder, Explorin',
			imageSrc: RamBansal,
			team: 'Dream Team',
		},
		{
			name: 'Mayank Bishnoyi',
			designation: 'Director, Mastercard',
			imageSrc: MayankBishnoi,
			team: `Mentors Club`,
		},
		{
			name: 'Utkarsh Raj',
			designation: 'Explorer at Trip',
			imageSrc: Utkarsh,
			team: `Dreamers Club`,
		},
		{
			name: 'Ehtesham Ahmad',
			designation: 'Sophomore, IIT Delhi',
			imageSrc: Ahmad,
			team: `Explorers Club`,
		},
	];

	const mentorCards = mentorCardsData.map((val, index) => (
		<div className='explorin-empire-card' key={index}>
			<main className='designation'>
				<strong>{val.name}</strong>
				<section>{val.designation}</section>
			</main>
			<div className='profile'>
				<img src={val.imageSrc} alt='empire-profile-pic' />
				<main>{val.team}</main>
			</div>
		</div>
	));

	return (
		<>
			<div className='empire-carousel-encloser-mobile'>
				<Carousel
					swipeable={true}
					draggable={true}
					showDots={true}
					responsive={responsive}
					ssr={!isMobile}
					deviceType={!isMobile ? 'desktop' : null}
					infinite={true}
					autoPlay={false}
					autoPlaySpeed={3000}
					additionalTransfrom={0}
					centerMode={false}
					keyBoardControl={true}
					customTransition='transform 300ms ease-in-out'
					transitionDuration={300}
					// transitionDuration={500}
					containerClass='carousel-container'
					removeArrowOnDeviceType={['tablet', 'mobile']}
					// deviceType={this.props.deviceType}
					dotListClass='custom-dot-list-style'>
					{mentorCards}
				</Carousel>
			</div>
			<button
				className='button showInMobileScreenOnly'
				onClick={() => window.location.assign('/empire')}>
				About the Empire
				<img alt='Plane' src={Plane} className='PlaneInButton' />
			</button>
		</>
	);
}
