// react imports
import React, { Suspense, lazy } from 'react';
import { render } from 'react-dom';
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Redirect,
} from 'react-router-dom';

// boostrap js
import 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import BasePage from './containers/BasePage';
const DET = lazy(() => import('./containers/DET'));
const Benefits = lazy(() => import('./containers/Benefits'));
const Empire = lazy(() => import('./containers/Empire'));

const DOMElement = document.getElementById('root');
render(
	<Router>
		<Switch>
			<Route exact path='/'>
				<BasePage />
			</Route>
			<Route exact path='/det'>
				<Suspense
					fallback={
						<div className='blank-screen-culprit' id='loader'></div>
					}>
					<DET />
				</Suspense>
			</Route>
			<Route exact path='/benefits'>
				<Suspense
					fallback={
						<div className='blank-screen-culprit' id='loader'></div>
					}>
					<Benefits />
				</Suspense>
			</Route>
			<Route exact path='/empire'>
				<Suspense
					fallback={
						<div className='blank-screen-culprit' id='loader'></div>
					}>
					<Empire />
				</Suspense>
			</Route>
			<Route path='*'>
				<Redirect to='/' />
			</Route>
		</Switch>
	</Router>,
	DOMElement
);
